<template>
  <div class="topbg-wrapper">
    <div class="top-bg">
      <div class="common-page--menu"><div class="link link-white" @click="$router.push({ name: 'Index' })">{{$t("首頁")}}&nbsp;>&nbsp;</div><div class="link link-white" @click="$router.go(-1)">IDO&nbsp;>&nbsp;</div><div class="link">{{$t("BACK基金私募購買")}}</div></div>
      <div class="top-header flex-center">
        <div class="header-center">
          {{ $t("BACK基金私募") }}<div class="point"></div>{{$t("購買")}}
        </div>
      </div>
    </div>
    <el-form class="from-box">
      <div class="form-inner" v-if="modeInfo">
        <div class="li flex-between">
          <div class="li-title">{{ $t("活動名稱") }}：</div>
          <div class="li-value">{{ $t("BACK基金會私募計劃") }}{{index}}</div>
        </div>
        <div class="li flex-between">
          <div class="li-title">{{ $t("活動時間") }}：</div>
          <div class="li-value">
            {{dateFormat("yyyy.MM.dd",new Date(modeInfo.start * 1000))}}
            -
            {{dateFormat("yyyy.MM.dd",new Date(modeInfo.end * 1000))}}
          </div>
        </div>
        <div class="li flex-between">
          <div class="li-title">{{ $t("初始釋放時間") }}：</div>
          <div class="li-value">
            {{dateFormat("yyyy.MM.dd",new Date(modeInfo.releaseTime * 1000))}}
          </div>
        </div>
        <div class="li flex-between">
          <div class="li-title">{{ $t("IDO價格") }}：</div>
          <div class="li-value">1 BACK = {{new BigNumber(modeInfo.price).dividedBy(Math.pow(10, 18)).toFixed()}} USDQ</div>
        </div>
        <div class="li flex-between">
          <div class="li-title">{{ $t("鎖倉時長") }}：</div>
          <div class="li-value">{{modeInfo.period}}{{ $t("天") }}</div>
        </div>
      </div>
      <div class="input-suffix flex-between" v-if="userData">
        <div class="input-label">{{$t("已私募數量")}}：</div>
        <div class="input-value">
          <span class="blue-color fw-bold">
            {{BigNumber(userData.icoAmount).dividedBy(Math.pow(10, 18)).toFixed(6, 1)}}
          </span> <span class="deep-color fw-bold">BACK</span>
        </div>
      </div>
      <div class="input-suffix flex-between">
        <span class="input-label"
          >{{$t('購買數量')}}</span
        >
        <el-input
          :placeholder="$t('請輸入購買數量')"
          class="flex"
          v-model="amount"
        >
          <template #append>
            <span class="deep-color fw-bold">BACK</span>
          </template>
        </el-input>
      </div>
      <div class="form-btn flex-between">
        <el-button
          type="primary"
          @click="$router.go(-1)"
          block
          round
          >{{ $t("取消") }}</el-button>
        <el-button
          v-if="!address"
          type="primary"
          block
          round
          @click="initWallet"
          >{{ $t("鏈接錢包") }}</el-button
        >
        <el-button
          v-else-if="
            BigNumber(USDQAmount).gt(0) &&
              (!allowance ||
                BigNumber(allowance).lte(0) ||
                BigNumber(allowance).lt(
                  BigNumber(USDQAmount)
                    .multipliedBy(Math.pow(10, 18))
                    .toFixed()
                ))
          "
          type="primary"
          block
          round
          :disabled="loading"
          :loading="loading"
          @click="approve"
        >{{ $t("授權") }}</el-button>
        <el-button
          v-else
          type="primary"
          block
          round
          :disabled="loading"
          :loading="loading"
          @click="toBuy"
          >{{ $t("購買") }}</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useRoute } from "vue-router";
import { useStore } from "vuex";
import backico from "@/utils/backico";
import { config } from "@/utils/config";
import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi";
import web3Data from "@/utils/initWeb3";
import { ElMessage } from "element-plus";
import { MaxUint256 } from "@ethersproject/constants";
import { useI18n } from "vue-i18n";
export default {
  components: {
  },
  setup() {
    const store = useStore();
    const { t } = useI18n();
    const route = useRoute();
    const data = reactive({
      BigNumber: BigNumber,
      index: route.query.index,
      web3: computed(() => store.state.provider),
      address: computed(() => store.state.accounts[0]),
      backicoContract: null,
      USDQContract: null,
      modeInfo: null,
      userData: null,
      balance: 0,
      allowance: 0,
      amount: '',
      USDQAmount: '',
      loading: false,
      initWallet: () => {
        if (!data.address) {
          web3Data.initWeb3();
        }
      },
      initContract: () => {
        data.backicoContract = new data.web3.eth.Contract(backico, config.backicoAddress);
        data.USDQContract = new data.web3.eth.Contract(tokenAbi, config.USDQAddress);
        data.initData();
      },
      initData: () => {
        data.getInfo();
        data.getBalance()
        data.getAllowance()
      },
      getInfo: async () => {
        let result = await data.backicoContract.methods.getModeByIndex(data.index).call();
        data.modeInfo = {...result}
        let userData = await data.backicoContract.methods.getUserData(data.address, data.index).call();
        data.userData = userData
      },
      getBalance: async () => {
        let result = await data.USDQContract.methods.balanceOf(data.address).call();
        data.balance = result
      },
      getAllowance: async () => {
        data.allowance = await data.USDQContract.methods.allowance(data.address, config.backicoAddress).call();
      },
      approve: () => {
        data.loading = true;
        data.USDQContract.methods.approve(config.backicoAddress, MaxUint256).send({ from: data.address })
        .then(result => {
          data.loading = false;
          data.getAllowance();
        })
        .catch(() => {
          data.loading = false;
        });
      },
      toBuy () {
        if (!Number(data.amount)) {
          ElMessage.error(t("請輸入購買數量"));
          return;
        }
        let amount = new BigNumber(data.amount).multipliedBy(Math.pow(10, 18)).toFixed();
        if (new BigNumber(data.balance).lt(amount)) {
          ElMessage.error(t("餘額不足"));
          return;
        }
        data.loading = true;
        data.backicoContract.methods.buy(data.index, amount)
        .estimateGas({ from: data.address })
        .then(gas => {
          data.backicoContract.methods.buy(data.index, amount).send({ from: data.address, gas: gas })
          .then(() => {
            ElMessage.success(t("交易成功"));
            data.loading = false;
            data.amount = ''
            data.initData()
          })
          .catch(() => {
            data.loading = false;
            ElMessage.error(t("交易失敗"));
          });
        })
        .catch(err => {
          console.log(err)
          data.loading = false;
          let errStr = err.toString()
          if (errStr.includes('Not ongoing')) {
            ElMessage.error('Not ongoing');
          }
          if (errStr.includes('Amount not enough')) {
            ElMessage.error('Amount not enough');
          }
        });
      }
    });
    if (data.address) {
      data.initContract();
    }
    watch(
      () => data.address,
      val => {
        if (val) {
          data.initContract();
        }
      }
    );
    watch(() => data.amount, (val) => {
      if (val) {
        let price = new BigNumber(data.modeInfo.price).dividedBy(Math.pow(10, 18)).toFixed()
        data.USDQAmount = new BigNumber(data.amount).multipliedBy(price).toFixed()
      } else {
        data.USDQAmount = ''
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="scss" scoped>
.topbg-wrapper .top-bg {
  height: 330px;
  background: url(~@/assets/image/pc/index_top.png) no-repeat center/100% 100%;
}
