      padding-right: 20px;
    }
  }
  .form-btn {
    margin: 22px 0 0;
    padding: 0 5px;
  }
}
.blue-color {
  color: #0956D0!important;
}
.deep-color {
  color: #111!important;
}
.fw-bold {
  font-weight: bold!important;
}
.form-btn {
  .el-button:first-child {
    margin-right: 30px;
    color: #0956D0;
    border: 1px solid #0956D0;
    background: #fff;
  }
}
