  }
  /deep/ .suffix-span {
    padding: 0 6px 0 0 !important;
    font-size: 15px !important;
    border-right: 1px solid #d4d4d4;
    margin-right: -6px;
  }
  /deep/ .from-box .el-input__inner {
    // width: 150px !important;
    font-size: 14px !important;
