  /deep/ .from-box .input-suffix .input-label {
    margin-right: 0;
  }
  /deep/ .from-box {
    margin-top: -150px;
  }
  /deep/ .el-input--suffix .el-input__inner {
    padding-right: 45px;
  }
  .from-box {
    padding: 0 15px;
  }
  /deep/ .el-input-group__append,
  .el-input-group__prepend {
    background: #eff2f7;
